import { Button, Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, TextField } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { useHistory } from "react-router";
import { Link } from "react-router-dom";
import { useCart } from "react-use-cart";
import "../styles/Cart.css";

// import product_data from "../data/product_data";

export default function Cart({ cart, where }) {
    const history = useHistory();
    const [toSend, setToSend] = useState({
        items: [],
        importe: [],
        subtotal: 0,
        total: "",
    })

    
    const { 
        isEmpty,
        items,
        cartTotal,
        setItems,
    } = useCart();
    
    useEffect(() => {
        setItems(cart);
        let res = 0;
        console.log(items);
        cart.forEach(item => {
            res = res + (item.quantity * item.price);
        });
        console.log(res);
        setToSend({
            items: cart,
            // importe: [],
            subtotal: res.toFixed(2),
            total: res.toFixed(2),    
        })
        console.log(cart);
        console.log(cartTotal);
    }, []);
    
    // function getTotal() {
    //     var res = 0;
    //     console.log(items);
    //     items.forEach(item => {
    //         res = res + (item.quantity * item.price);
    //     });
    //     console.log(res);
    //     return res;
    // }


    const aux = [];

    function getImporte(quantity, price) {
        var res = quantity * price;
        aux.push(res);
        return res;
    }

    function getSubtotal() {
        var res = 0;
        items.forEach(item => {
            res = res + (item.quantity * item.price);
        });
        return res;
    }


    function confirmSale() {

        console.log(toSend);
        history.push({
            pathname: '/NuevaVenta', state: {
                toSend: toSend,
            }
        })
    }

    if (isEmpty) return (
        <div>
            <h1>Carrito Vac√≠o</h1>
            <Link to='/'></Link>
        </div>
    )
    return (
        <React.Fragment>

            <div className="table_container">

                <TableContainer component={Paper}>
                    <Table style={{ fontSize: "1.25rem" }} aria-label="spanning table">
                        <TableHead>
                            <TableRow>
                                <TableCell align="left" colSpan={3}>
                                    √çtems
                                </TableCell>
                                <TableCell align="right">Importes</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell>Descripci√≥n</TableCell>
                                <TableCell align="right">Cantidad</TableCell>
                                <TableCell align="right">Precio unit.</TableCell>
                                <TableCell align="right">Importe</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {items.map((item, index) => {
                                return (
                                    <TableRow>
                                        <TableCell>{item.descrip}</TableCell>
                                        {/*ac√° ir√≠a lo que es botones para ADD o SUBTRACT item qtty*/}
                                        <TableCell align="right">{item.quantity} [{item.quantity > 1 ?  item.tipoUnidad+"s" : item.tipoUnidad}]</TableCell>
                                        <TableCell align="right">$ {item.price}</TableCell>
                                        <TableCell align="right">$ {getImporte(item.quantity, item.price).toFixed(2)}</TableCell>
                                    </TableRow>
                                )
                            })}
                            <TableRow>
                                <TableCell rowSpan={4} colSpan={1} />
                                <TableCell style={{ fontWeight: "bolder" }}>Subtotal</TableCell>
                                <TableCell colSpan={2} align="right">$ {cartTotal.toFixed(2)}</TableCell>
                            </TableRow>
                            {/* <TableRow>
                                <TableCell style={{ padding: "0 16px 0 16px", fontWeight: "bolder" }}>A√±adir Descuento: </TableCell>
                                <TableCell colSpan={2} align="right" style={{ padding: "0 16px 0 16px" }}>
                                    <TextField fullWidth id="discount" label="Ingrese un cup√≥n de descuento." />
                                </TableCell>
                            </TableRow> */}
                            <TableRow>
                                <TableCell style={{ fontWeight: "bolder" }}>Cargos por env√≠o a domicilio:</TableCell>
                                <TableCell colSpan={2} align="right">$ 00,0</TableCell>
                            </TableRow>
                            <TableRow>
                                <TableCell style={{ fontWeight: "bolder" }}>Total</TableCell>
                                <TableCell colSpan={2} align="right">$ {cartTotal.toFixed(2)}</TableCell>
                            </TableRow>
                        </TableBody>
                    </Table>
                </TableContainer>

            </div>

            <Button className="root" onClick={() => { confirmSale() }}>Confirmar Carrito</Button>

        </React.Fragment>
    );
};

